<template>
  <div class='history'>
    <div class='title'>{{this.$t('User.History')}}</div>

    <el-table class='table' :data="posts" stripe :show-header='false'>
      <el-table-column prop="typeFormat" min-width="140"></el-table-column>
      <el-table-column prop="item" min-width="300"></el-table-column>
      <el-table-column prop="time" min-width="170"></el-table-column>
      <el-table-column miin-width="100">
        <template slot-scope="scope">
          <span class='open' @click="showPage(scope.row.type, scope.row.id)">{{$t('User.HistoryCheck')}}</span>
        </template>
      </el-table-column>
    </el-table>
  </div>
</template>

<script>
import axios from 'axios'
import Cookies from 'js-cookie'
import * as server from '../../../server.js'

export default {
  name: 'user-history',
  data () {
    return {
      posts: [],
      comments: [],
      likes: []
    }
  },
  methods: {
    getUserHistory () {
      if (!Cookies.get('loginCookie')) {
        this.goLogin();
        return;
      }

      const loadingInstance = this.loading();

      axios({
        method: 'post',
        url: server.default.userHistory,
        data: new URLSearchParams({
          'cookie': Cookies.get('loginAs')
        })
      }).then((response) => {
        loadingInstance.close();
        if (response.status != 200) {
          this.alert(this.$t('System.NetworkIssue'), this.$t('System.Error'))
        }
        else {
          if (response.data == 'USER_NOMATCH') {
            this.alert(this.$t('System.IllegalRequest'), this.$t('System.Error'));
          }
          else if (response.data.status == 'USER_FOUND') {
            this.posts = response.data.posts;
            for (let i in this.posts) {
              this.posts[i].typeFormat = this.typeFormat(this.posts[i].type);
              this.posts[i].time = this.formatTime(this.posts[i].time);
            }
            // this.comments = response.data.comments;
            // this.likes = response.data.likes;
          }
          else {
            this.alert('Unexpected Response!', this.$t('System.Error'));
          }
        }
      });
    },
    tableRowClass ({ row, rowIndex }) {
      row.row_index = rowIndex;
    },
    showPage (type, id) {
      switch (type) {
        case 'h':
          this.$router.push({ name: 'hollowDetail', params: { id: id } })
          break;
        case 'm':
          this.$router.push({ name: 'marketDetail', params: { id: id } })
          break;
        case 'n':
          this.$router.push({ name: 'newsDetail', params: { id: id } })
          break;
        case 'l':
          this.$router.push({ name: 'lafDetail', params: { id: id } })
          break;
      }
    },
    typeFormat (type) {
      switch (type) {
        case 'h':
          return this.$t('Hollow.Title');
        case 'm':
          return this.$t('Market.Title');
        case 'n':
          return this.$t('News.Title');
        case 'l':
          return this.$t('Laf.Title');
      }
      return '';
    }
  },
  mounted () {
    this.getUserHistory();
  }
}
</script>

<style scoped>
.history {
  margin-left: 5%;
}
.title {
  font-size: 25px;
}
.table {
  text-align: left;
  margin-top: 25px;
  font-size: 20px;
}
.el-table ::v-deep th {
  padding: 0;
}
.el-table ::v-deep .el-table__cell {
  padding-top: 20px;
  padding-bottom: 20px;
}
.open {
  cursor: pointer;
}
</style>